<template>
  <div class="authLogin">
    {{ msg }}
  </div>
</template>

<script>
export default {
  name: "AuthLogin",
  data() {
    return {
      code: "",
      msg: "授权中...",
    };
  },
  created() {
    this.code = this.$route.params.code;
    let page = this.$route.params.page;
    localStorage.removeItem('userInfo')
    localStorage.removeItem('project')
    localStorage.removeItem('token')
    if(page == 'home')
      this.authLogin();
    else{
      this.toFormPage()
    }
  },
  methods: {
    authLogin() {
      this.$http.auth.authLogin({authCode:this.code}).then(res=>{
        if(res.data && res.data.token){
          localStorage.setItem('token',res.data.token)
          this.$router.push('/home')
        }else{
          // console.log('跳转空页面');
          this.$router.push('/noData')
        }
      })
    },
    toFormPage(){
      this.$http.auth.getUserByAuthcode({authCode:this.code}).then(res=>{
        if(res.code == 200){
          localStorage.setItem('userInfo',JSON.stringify(res.data))
          this.$router.push('/complaintForm')
        }else{
          this.msg = res.msg
        }
      })
    }
  },
  mounted() {},
};
</script>

<style lang="less" scoped>
.authLogin {
    font-size: 14px;
    text-align: center;
}
</style>